@charset "utf-8";
body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl, dd {
	margin: 0;
	padding: 0;
}
h1 {
	font-size: 16px;
}
a {
	text-decoration: none;
	color: #000;
}
ul, ol {
	list-style: none;
}
img {
	max-width: 100%;
	vertical-align: top;
	border: none;
}
body {
	font-family: "Roboto", "Noto Sans KR", sans-serif;
	font-size: 1em;
}
body.fixed {
	overflow-y: hidden;
}
#start{
	position: relative;
}
#start header{
	position: absolute;
	left: 0;
	top: 0;
	z-index: 1000;
	width: 100%;
}
#start header.active{
	position: fixed;
	height: 70px;
	background: rgba(255,255,255,.1);
	transform: translateY(0);
}
#start header .header_inner{
	position: relative;
	display: flex;
	padding: 0 5vw;
	justify-content: space-between;
	align-items: center;
	height: 70px; 
}
#start header .header_inner .logo{
	width: 100px;
	height: 40px;
}
#start header .header_inner .logo a{
	font-family: "Jomolhari";
	color: #fff;
	font-size: 1.25em;
	font-weight: 700;
}
#pc {
	z-index: 1000;
	height: 70px;

}
#pc::after{
	content: "";
	position: absolute;
	top: 63px;
	width: 100%;
}
#pc > ul {
	display: flex;
	justify-content: center;
}
#pc > ul.active{
	height: 90px;
}
#pc > ul > li {
	position: relative;
	margin: 0 20px;
}
#pc > ul > li:last-child {
	margin-right: 0;
}
#pc > ul > li > a {
	display: block;
	/* padding: 15px 0; */
	margin-top: 15px;
	line-height: 40px;
	white-space: nowrap;
	font-size: 0.875em;
	font-weight: 500;
	color: #fff;
}
