} */
#pc li a:hover, #pc li a.on{
	color: #0EC4CF;
	border-bottom: 2px solid #0EC4CF;
}
#mobile{
	position: fixed;
	right: -700px;
	top: 0;
	z-index: 1000;
	width: 240px;
	height: 100vh;
	background-color: #000;
	transition: right 0.3s;
}
#mobile.active{
	right: 0;
}
#mobile > ul{
	padding: 10vh 5vw;
	background-color: #000;
}
#mobile > ul > li{
	border-bottom: 1px solid #353535;
}
#mobile > ul > li.active > a{
	color: #72efc1;
}
#mobile > ul > li.active ul{
	display: block;
}
#mobile ul li a{
	display: block;
	line-height: 3;
	text-indent: 20px;
	text-decoration: none;
	color: #0ec4cf;
	font-size: 1.25em;
	font-weight: 700;
}
