#tab{
	display: none;
	position: absolute;
	right: 16px;
	top: 24px;
	z-index: 1100;
	width: 28px;
	height: 23px;
	background: url(./images/ico_tab_open.png) no-repeat 0 0;
	transition: right 0.3s;
}
#tab.active {
	right: 16px;
	width: 24px;
	height: 23px;
	background-image: url(./images/ico_tab_close.png);
}
#dim {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0,0,0,.5);
}
#dim.active {
	display: block;
}
.main{
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	height: 100vh;
	background-color: #000;
	overflow: hidden;
}
.main::after{
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	z-index: 1;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.5);
}
.main .inner {
	position: absolute;
	z-index: 100;
	padding: 15vh 5vw 0 5vw;
	width: 100%;
	box-sizing: border-box;
}
.main .keytext{
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	text-align: center;
	color: #fff;
}
.main .keytext h2{
	font-size: 7vw;
	font-weight: 600;
	color: #0ec4cf;
	/* opacity: 0;
	transform: translateX(-50px);
	transition: opacity 0.4s, transform 0.6s;
	transition-delay: 0.4s; */
}
/* .main .keytext h2::after{
	content: "";
	position: absolute;
	width: 600px;
	height: 2px;
	left: 300px;
	bottom: 20px;
	background-color: #f1f1ef;
} */
.main .keytext .moto{
	margin-left: 40px;
	width: 100%;
	text-align: right;
}
.main .keytext .moto p{
	font-size: 1em;
	font-weight: 700;
	line-height: 4;
	letter-spacing: 3px;
	border-bottom: 1px solid #0ec4cf;
	/* opacity: 0;
	color: #0ec4cf;
	transform: translateX(50px);
	transition: opacity 0.4s, transform 0.6s;
	transition-delay: 0.0s; */
}
.main .keytext .moto p span{
	font-weight: 400;
}
.main .keytext .moto > span{
	display: block;
	line-height: 3.2;
	font-size: 0.875em;
	font-weight: 500;
	/* opacity: 0;
	transform: translateX(50px);
	transition: opacity 0.4s, transform 0.6s;
	transition-delay: 0.6s; */
}
/* #start.active .main .keytext h2,
#start.active .main .keytext .moto p,
#start.active .main .keytext .moto > span{
	opacity: 1;
	transform: translateX(0);
} */
.main .box{
	margin-top: 20vh;
}
.main .box ul{
	display: flex;
	/* flex-wrap: wrap; */
	justify-content: space-between;
	gap: 30px;
}
.main .box ul li{
	width: calc((100% - 60px)/5);
	/* opacity: 0;
	transition: opacity 0.4s, transform 0.6s; */
}
.main .box ul li{
	transform: translateY(100px);
}
.main .box ul li:nth-child(1){
	transition-delay: 2s;
}
.main .box ul li:nth-child(2){
	transition-delay: 2.2s;
}
.main .box ul li:nth-child(3){
	transition-delay: 2.4s;
}
.main .box ul li:nth-child(4){
	transition-delay: 2.6s;
}
.main .box ul li:nth-child(5){
	transition-delay: 2.8s;
}
/* #start.active .main .box ul li{
	opacity: 1;
	transform: translateY(0);
} */
.main .box ul li a{
	color: #fff;
}
.main .box ul li a h3{
	position: relative;
	padding: 15px 0 10px 40px;
	font-size: 0.875em;
}
.main .box ul li a h3::after{
	content: "";
	position: absolute;
	width: 100%;
	height: 1px;
	left: 0;
	bottom: 0;
	background-color: rgba(255,255,255,.2);
}
.main .box ul li a span.em{
	position: absolute;
	left: 10px;
	top: 15px;
	font-weight: 700;
	color: #0ec4cf;
}
.main .box ul li a p{
	padding: 30px 10px;
	line-height: 1.8;
	font-size: 0.813em;
	font-weight: 400;
	opacity: 0.5;
}

@media only screen and (max-width: 1240px) {
	.main .box ul li a p {
		display: none;
	}
}
@media only screen and (max-width: 720px) {
	#pc{
		display: none;
	}
	#tab{
			display: block;
		}
	/* #mobile{
		width: 100%;
	} */
	.main .keytext {
		flex-direction: column;
		align-items: flex-start;
	}
	.main .keytext h2 {
		font-size: 11vw;
	}
	.main .keytext .moto {
		margin-left: 0;
		text-align: left;
	}
	.main .box {
		display: none;
	}
}

